//  mixin 
export default {
  methods: {
    // 
    format(e) {
      let {
        name,
        value
      } = e.target.dataset
      if (!name) return
      // console.log('format', name, value)
      this.editorCtx.format(name, value)
    },
    //  Context 
    onStatusChange(e) {
      const formats = e.detail
      this.formats = formats
    },
    // 
    undo() {
      this.editorCtx.undo()
    },
    // 
    redo() {
      this.editorCtx.redo()
    },
    // 
    insertDivider() {
      this.editorCtx.insertDivider();
    },
    // 
    removeFormat() {
      this.editorCtx.removeFormat()
    },
    // 
    clear() {
      var that = this
      uni.showModal({
        title: '',
        content: '',
        cancelText: "", //   
        confirmText: "", //  
        confirmColor: '#F54E40', //
        cancelColor: '#000', //
        success: function(res) {
          if (res.confirm) {
            that.editorCtx.clear()
          }
        }
      });
    },
  },
}