import { mapState } from 'vuex'

//  mixin 
export default {
  computed: {
    ...mapState('user', ['userInfo']),
  },
  onShow() {
    // 
    if(this.userInfo.userType !== ""){
      uni.switchTab({
        url: '/pages/home/home'
      })
      return
    }
  },
  methods: {
    
  },
}